/*--------------------------------------------------------------
# Theme SASS
- Contains styles for blocks, templates and other assets.
--------------------------------------------------------------*/
/*--------------------------------------------------------------
Media queries modeled on those found in Bootstrap 4.
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Single Participant

1. Mobile
2. Desktop
--------------------------------------------------------------*/
/*--------------------------------------------------------------
1. Mobile
--------------------------------------------------------------*/
.single-participant #main-content {
		margin-bottom: 4rem;
}
.single-participant #main-content .participant-social {
		margin: 1rem 0 0 0;
		padding: 0;
		font-size: 2rem;
}
.single-participant #main-content .participant-social li {
		list-style-type: none;
		display: inline;
		margin-right: 1rem;
}
.single-participant #main-content .participant-social li a:visited {
		color: #8c1d40;
}
.single-participant #main-content .project-icon img {
		display: none;
}
.single-participant #main-content .project-mentor {
		margin: 2rem 0;
}
.single-participant #main-content .cta-buttons svg {
		margin-left: 0.5rem;
}
.single-participant #main-content .cta-buttons .btn {
		margin-bottom: 1.5rem;
}
.single-participant .row-featured {
		margin-top: 1rem;
}
.single-participant .row-featured .featured-image-set {
		align-items: flex-start;
}
.single-participant .row-featured a.featured-image {
		border: 1px solid #ffffff;
		margin-right: 1rem;
		margin-top: 1rem;
		display: inline-flex;
}
.single-participant .row-featured a.featured-image:last-child {
		margin-right: none;
}
.single-participant .row-featured a.featured-image:hover {
		border-color: #8c1d40;
}
.single-participant .uds-modal-container {
		width: auto;
		max-width: 800px;
		height: auto;
}
.single-participant .uds-modal-container .qr-code-wrap h3 {
		padding-top: 1rem;
}
.single-participant .uds-modal-container .qr-code-wrap img {
		max-width: 300px;
		height: auto;
		float: left;
		margin-right: 1.5rem;
}

@media (max-width: calc(768px - 0.02px)) {
		#additional-projects-title h3,
		#related-projects-title h3 {
				height: 70px;
		}
}
/*--------------------------------------------------------------
2. Desktop
--------------------------------------------------------------*/
@media (min-width: calc(768px)) {
		.single-participant #main-content .participant-name {
				margin-bottom: 0.5rem;
		}
		.single-participant #main-content .participant-major {
				margin-top: 0.5rem;
				margin-bottom: 0.5rem;
		}
		.single-participant #main-content .hometown,
		.single-participant #main-content .graduation-date {
				margin-bottom: 0;
		}
		.single-participant #main-content .project-icon img {
				margin: 2rem 0 3rem 0;
				display: block;
				max-width: 8rem;
		}
		.single-participant #main-content .presentation-details {
				margin-top: 0;
		}
		.single-participant #main-content .cta-buttons .btn + .btn {
				margin-left: 1rem;
		}
}
/*--------------------------------------------------------------
# About Page

1. Desktop
--------------------------------------------------------------*/
.single-furiproject #main-content {
		margin-bottom: 6rem;
}
.single-furiproject #main-content .cta-buttons .btn + .btn {
		margin-left: 1rem;
}
.single-furiproject #main-content .cta-buttons svg {
		margin-left: 0.5rem;
}
.single-furiproject .theme-icon {
		max-width: 8rem;
		float: right;
}
.single-furiproject #secondary .card {
		height: auto;
}
.single-furiproject #secondary .card .card-img-top {
		object-position: 100% 20%;
}

/*--------------------------------------------------------------
Taxonomy Term Page: Faculty Mentor
Taxonomy Term Page: Industry Sponsor

1. All sites
--------------------------------------------------------------*/
.tax-faculty_mentor #page-header,
.tax-industry_sponsor #page-header {
		padding-top: 3rem;
		padding-bottom: 3rem;
}
.tax-faculty_mentor #page-header .isearch-image,
.tax-industry_sponsor #page-header .isearch-image {
		border-radius: 50%;
		object-fit: cover;
		width: 250px;
		height: 250px;
}
.tax-faculty_mentor #page-header .acf-image,
.tax-industry_sponsor #page-header .acf-image {
		padding-right: 1rem;
}
.tax-faculty_mentor #page-header .mentor-name,
.tax-industry_sponsor #page-header .mentor-name {
		margin-top: 0;
}
.tax-faculty_mentor #page-header .info-bar,
.tax-industry_sponsor #page-header .info-bar {
		display: flex;
		flex-direction: row;
		justify-content: start;
		gap: 1rem;
		margin-bottom: 2rem;
}
.tax-faculty_mentor #page-header .info-bar a svg,
.tax-industry_sponsor #page-header .info-bar a svg {
		margin-right: 0.5rem;
}
.tax-faculty_mentor #page-header .info-bar .social-icons,
.tax-industry_sponsor #page-header .info-bar .social-icons {
		margin-bottom: 0;
		padding-left: 1rem;
}
.tax-faculty_mentor #page-header .info-bar .social-icons li,
.tax-industry_sponsor #page-header .info-bar .social-icons li {
		list-style-type: none;
		display: inline-block;
		margin-right: 1rem;
		font-size: 1.5rem;
}
.tax-faculty_mentor #page-header .info-bar .social-icons li a,
.tax-industry_sponsor #page-header .info-bar .social-icons li a {
		color: #747474;
}
.tax-faculty_mentor #quote-content .uds-blockquote,
.tax-industry_sponsor #quote-content .uds-blockquote {
		background-color: #fafafa;
		border: 1px solid #e8e8e8;
		margin-bottom: 2rem;
}
.tax-faculty_mentor #blog-post-excerpt .post-teaser-box,
.tax-industry_sponsor #blog-post-excerpt .post-teaser-box {
		background-color: #191919;
		color: #ffffff;
		border: 1px solid #e8e8e8;
		padding: 2rem;
		margin: 3rem 0 2rem 0;
		position: relative;
}
.tax-faculty_mentor #blog-post-excerpt .post-teaser-box h3,
.tax-industry_sponsor #blog-post-excerpt .post-teaser-box h3 {
		background-color: #ffc627;
		color: #191919;
		font-weight: 700;
		display: inline-block;
		padding: 0.5rem 1rem;
		margin: 0;
		height: 40px;
		position: absolute;
		top: -1.5rem;
}
.tax-faculty_mentor #blog-post-excerpt .post-teaser-box .lead a,
.tax-industry_sponsor #blog-post-excerpt .post-teaser-box .lead a {
		display: none;
}
.tax-faculty_mentor #blog-post-excerpt .post-teaser-box .read-more-button,
.tax-industry_sponsor #blog-post-excerpt .post-teaser-box .read-more-button {
		text-align: right;
}
.tax-faculty_mentor #content,
.tax-industry_sponsor #content {
		margin-bottom: 4rem;
}
.tax-faculty_mentor #content .social-icons,
.tax-industry_sponsor #content .social-icons {
		padding: 0;
		margin: 0;
}
.tax-faculty_mentor #content .social-icons li,
.tax-industry_sponsor #content .social-icons li {
		list-style-type: none;
		display: inline;
		margin-left: 1rem;
		font-size: 32px;
}
.tax-faculty_mentor #content .social-icons a:visited,
.tax-industry_sponsor #content .social-icons a:visited {
		color: #8c1d40;
}
.tax-faculty_mentor .related-projects,
.tax-industry_sponsor .related-projects {
		background-color: #e8e8e8;
		padding: 4rem 0;
}
.tax-faculty_mentor .related-projects .col-md-4,
.tax-industry_sponsor .related-projects .col-md-4 {
		margin-bottom: 2rem;
}
.tax-faculty_mentor .related-projects + .section-head,
.tax-industry_sponsor .related-projects + .section-head {
		background-color: #e8e8e8;
}
.tax-faculty_mentor #snapshot-global-title,
.tax-faculty_mentor #snapshot-global,
.tax-industry_sponsor #snapshot-global-title,
.tax-industry_sponsor #snapshot-global {
		display: none;
}

.tax-faculty_mentor blockquote + p {
		margin-top: 4rem;
}
.tax-faculty_mentor .post-teaser-box .mb-2 a {
		display: none;
}

@media (max-width: calc(768px - 0.02px)) {
		.tax-faculty_mentor #page-header .info-bar {
				flex-direction: column;
		}
}
/*--------------------------------------------------------------
2. Taxonomy Term Page: Industry Sponsor
-------------------------------------------------------------- */
.tax-industry_sponsor .btn svg {
		margin-right: 0.5rem;
}
.tax-industry_sponsor #snapshot-global-title {
		background-color: #e8e8e8;
}

/*--------------------------------------------------------------
Taxonomy Term Page: symposium_date
Taxonomy Term Page: research_theme
Taxonomy Term Page: presentation_type
--------------------------------------------------------------*/
.tax-symposium_date .archive-header,
.tax-research_theme .archive-header,
.tax-presentation_type .archive-header {
		padding: 4rem 0;
}
.tax-symposium_date .archive-header .subtitle,
.tax-research_theme .archive-header .subtitle,
.tax-presentation_type .archive-header .subtitle {
		-webkit-box-shadow: -0.15em 0 0 #ffc627, 0.15em 0 0 #ffc627;
		box-shadow: -0.15em 0 0 #ffc627, 0.15em 0 0 #ffc627;
		background: #ffc627;
		color: #191919;
		font-size: 1.5rem;
		font-weight: 700;
		line-height: 1.75rem;
		letter-spacing: -0.0525rem;
		margin-left: 0.15em;
		display: inline-block;
}
.tax-symposium_date .archive-header h1,
.tax-research_theme .archive-header h1,
.tax-presentation_type .archive-header h1 {
		margin-top: 0;
}
.tax-symposium_date .archive-header .theme-icon,
.tax-research_theme .archive-header .theme-icon,
.tax-presentation_type .archive-header .theme-icon {
		float: right;
		max-width: 300px;
		margin-left: 2rem;
}
.tax-symposium_date .uds-section,
.tax-research_theme .uds-section,
.tax-presentation_type .uds-section {
		padding: 4rem 0;
}
.tax-symposium_date #table-wrapper .dataTables_wrapper .dataTables_info,
.tax-research_theme #table-wrapper .dataTables_wrapper .dataTables_info,
.tax-presentation_type #table-wrapper .dataTables_wrapper .dataTables_info {
		padding: 0.25em 0 0 0.5em;
		width: 49%;
		float: left;
}
.tax-symposium_date #table-wrapper .dataTables_wrapper .dataTables_filter input,
.tax-research_theme #table-wrapper .dataTables_wrapper .dataTables_filter input,
.tax-presentation_type #table-wrapper .dataTables_wrapper .dataTables_filter input {
		width: 300px;
}
.tax-symposium_date #table-wrapper .dataTables_wrapper table > thead > tr > th,
.tax-research_theme #table-wrapper .dataTables_wrapper table > thead > tr > th,
.tax-presentation_type #table-wrapper .dataTables_wrapper table > thead > tr > th {
		background-color: #8c1d40;
		color: #ffffff;
}

.tax-presentation_type .archive-header {
		padding-right: 16rem;
}

/*--------------------------------------------------------------
Mobile considerations
--------------------------------------------------------------*/
@media (max-width: calc(768px - 0.02px)) {
		.tax-presentation_type .archive-header {
				padding-right: 0;
		}
		.tax-research_theme .archive-header .theme-icon {
				max-width: 64px;
				margin-left: 0.5rem;
		}
		.tax-symposium_date .archive-header {
				padding: 0 0 1rem 0;
		}
		.archive .archive-header {
				padding-top: 0;
				padding-bottom: 2rem;
		}
		.archive .archive-header .lead {
				font-size: 1rem;
		}
		.archive #table-wrapper .dataTables_wrapper .dataTables_info {
				padding: 0 0 1rem 0;
				width: 100%;
				float: none;
		}
		.archive #table-wrapper .dataTables_wrapper .dataTables_filter input {
				width: 200px;
		}
		.archive #table-wrapper .dataTables_wrapper .table {
				font-size: 10px;
		}
}
/*--------------------------------------------------------------
# Page Tempalte: Symposium

1. Desktop
2. Mobile
3. Symposium cards
4. Additional cards
--------------------------------------------------------------*/
.page-template-symposium .above-filters h4 {
		margin-top: 0.5rem;
		margin-bottom: 0;
}
.page-template-symposium button#filter-mobile-panel {
		display: none;
		position: absolute;
		top: 4.25rem;
		right: 1rem;
}
.page-template-symposium button#filter-mobile-panel svg {
		margin-right: 0.5rem;
}
.page-template-symposium .filter-container #filter-reset,
.page-template-symposium .filter-container #filter-shuffle {
		margin-top: 1rem;
		margin-bottom: 4rem;
		text-align: right;
}
.page-template-symposium .filter-container #filter-reset svg,
.page-template-symposium .filter-container #filter-shuffle svg {
		margin-right: 0.5rem;
}
.page-template-symposium .filter-container #filter-reset + button,
.page-template-symposium .filter-container #filter-shuffle + button {
		margin-left: 1rem;
}
.page-template-symposium .filter-container form {
		margin-top: 1rem;
}
.page-template-symposium .filter-container form:first-child {
		margin-top: 0;
}
.page-template-symposium .filter-container label {
		display: block;
}
.page-template-symposium .filter-container #presentation-type-filters .form-check label {
		font-size: 1.25rem;
}
.page-template-symposium .filter-container #presentation-type-filters .form-check-input {
		margin-top: 0.5rem;
}
.page-template-symposium .filter-container #research-theme-filters input {
		display: none;
}
.page-template-symposium .filter-container #research-theme-filters input ~ label.form-check-label-disabled {
		display: block;
}
.page-template-symposium .filter-container #research-theme-filters input ~ label.form-check-label-enabled {
		display: none;
}
.page-template-symposium .filter-container #research-theme-filters input:checked ~ label.form-check-label-disabled {
		display: none;
}
.page-template-symposium .filter-container #research-theme-filters input:checked ~ label.form-check-label-enabled {
		display: block;
}
.page-template-symposium .filter-container #research-theme-filters .research-theme-icon {
		width: 50px;
		height: auto;
}
.page-template-symposium .filter-container .bootstrap-select button.dropdown-toggle {
		border-color: #191919;
		color: #191919;
}
.page-template-symposium .filter-container .bootstrap-select button.dropdown-toggle.disabled {
		border-color: #d0d0d0;
		color: #d0d0d0;
}
.page-template-symposium .filter-container .bootstrap-select button.bs-select-all {
		display: none;
}
.page-template-symposium .filter-container .bootstrap-select button.bs-deselect-all {
		border-radius: 400rem;
}
.page-template-symposium .filter-container .bootstrap-select .dropdown-menu.show {
		width: 100%;
}
.page-template-symposium .filter-container .bootstrap-select .dropdown-menu.show li a span.text {
		word-wrap: break-word;
		white-space: normal;
		font-size: 14px;
}
.page-template-symposium .filter-container .bootstrap-select .dropdown-menu > .inner:focus {
		box-shadow: none !important;
}

/*--------------------------------------------------------------
X. Mobile
--------------------------------------------------------------*/
@media (max-width: calc(768px - 0.02px)) {
		.page-template-symposium button#filter-mobile-panel {
				display: block;
		}
		.page-template-symposium #symposium-grid-col {
				min-height: 100vh;
		}
		.page-template-symposium .above-filters h4 {
				font-size: 1.5rem;
				margin-top: 1.5rem;
		}
		.page-template-symposium .card-symposium {
				min-height: 0;
		}
}
/*--------------------------------------------------------------
3. Symposium cards
--------------------------------------------------------------*/
#symposium-grid .card-symposium {
		padding-top: 0;
		min-height: 497px;
		margin-bottom: 1.5rem;
}
#symposium-grid .card-symposium .card-header {
		padding-top: 1rem;
}
#symposium-grid .card-symposium .card-header a {
		text-decoration: none;
}
#symposium-grid .card-symposium .card-header a:hover {
		text-decoration: underline;
}
#symposium-grid .card-symposium .card-header .participant {
		margin: 0;
		padding-bottom: 0.5rem;
}
#symposium-grid .card-symposium .card-header .major {
		margin: 0;
		font-weight: 400;
}
#symposium-grid .card-symposium .card-title,
#symposium-grid .card-symposium .card-text {
		font-size: 14px;
		line-height: 18px;
}
#symposium-grid .card-symposium .card-title a {
		text-decoration: underline;
		font-size: 18px;
}

/*--------------------------------------------------------------
3. Featured Symposium cards
--------------------------------------------------------------*/
#symposium-grid .card-ranking {
		width: calc(66.66% - 24px);
		margin-left: 12px;
		margin-right: 12px;
}
#symposium-grid .card-ranking .header h4 {
		max-width: none;
}
#symposium-grid .card-ranking .header h4 a {
		font-size: 1.25rem;
		font-weight: 700;
}
#symposium-grid .card-ranking .header h4 span {
		display: block;
		font-size: 16px;
		font-weight: 400;
}
#symposium-grid .card-ranking .content .header .btn-expand svg {
		font-size: 1rem;
		float: none;
		margin-left: 2px;
}

/*--------------------------------------------------------------
4. Additional cards
--------------------------------------------------------------*/
.card.card-hover:hover {
		cursor: auto;
}
.card.card-hover:active {
		transform: scale(1.05);
}

/*--------------------------------------------------------------
X. Scotch Panel
--------------------------------------------------------------*/
@media (max-width: calc(768px - 0.02px)) {
		.page-template-symposium #page-wrapper {
				padding-right: 0;
		}
		.page-template-symposium #main-content {
				padding: 0;
		}
		.page-template-symposium .row {
				padding-right: 2rem;
		}
}
.scotch-is-showing #scotch-panel {
		box-shadow: -3px 0px 3px 0px rgba(0, 0, 0, 0.3);
}

#scotch-panel {
		transition: 0.3sec;
		padding: 0;
		background-color: #fafafa;
}
#scotch-panel .filter-container {
		border-left: 1px solid #e8e8e8;
		padding: 1rem;
}

/*--------------------------------------------------------------
# SASS for various custom blocks

1. Program Descriptions
2. Current Snapshot
3. Research Themes
4. Featured Students / Sponsored Mentors
5. Featured project carousel
6. FURI Alumni
7. Life after FURI graph
--------------------------------------------------------------*/
@media (min-width: calc(768px)) {
		#program-descriptions div[class*=col] {
				padding: 1rem;
		}
}
@media (max-width: calc(768px - 0.02px)) {
		#program-descriptions {
				margin-top: 4rem;
		}
}
/*--------------------------------------------------------------
2. Current Snapshot
--------------------------------------------------------------*/
#current-snapshot {
		display: block;
}
#current-snapshot .stat-package {
		display: flex;
		flex-direction: row;
		justify-content: center;
		margin-bottom: 1.5em;
		padding: 10px;
}
#current-snapshot .stat-package h3 {
		font-size: 1.75rem;
		line-height: 0.95;
		color: #191919;
		font-weight: 700;
		text-align: right;
		margin-right: 20px;
}
#current-snapshot .stat-package h3 span {
		display: block;
		color: #ffc627;
}
#current-snapshot .stat-package .counter {
		font-size: 3.5rem;
		line-height: 0.95;
		color: #191919;
		font-weight: 700;
		align-self: center;
		margin-left: 1rem;
}

/*--------------------------------------------------------------
3. Research Themes
--------------------------------------------------------------*/
#research-themes .row-header {
		margin-bottom: 2rem;
}
#research-themes .container {
		background-color: #ffffff;
		padding: 2rem;
}
#research-themes .media {
		margin-bottom: 4rem;
}
#research-themes .media img {
		width: 5rem;
		height: auto;
}

/*--------------------------------------------------------------
3dot5. Project Graph (Placeholder)
--------------------------------------------------------------*/
#project-graph .chart-placeholder {
		height: 400px;
		width: auto;
		background-image: url("../img/bar-chart-placeholder.png");
		background-size: cover;
		position: relative;
}
#project-graph h2 {
		position: absolute;
		top: 3rem;
		left: 6rem;
}

/*--------------------------------------------------------------
4. Featured Students / Sponsored Mentors
--------------------------------------------------------------*/
#featured-mentors .mentor {
		margin-bottom: 4rem;
		display: grid;
		grid-template-columns: 180px 1fr;
		grid-template-rows: auto auto;
		grid-column-gap: 4rem;
		grid-row-gap: 1rem;
}
#featured-mentors .mentor h3 {
		grid-column: 1/span 2;
		grid-row: 1;
}
#featured-mentors .mentor img {
		grid-column: 1;
		grid-row: 2;
		border-radius: 100%;
}
#featured-mentors .mentor .featured-mentor-post p:first-child > a {
		display: none;
}
#featured-mentors .mentor .featured-mentor-post .read-more {
		display: inline-block;
}
#featured-mentors .mentor .featured-mentor-post svg {
		color: #ffc627;
		width: 3rem;
		height: auto;
		grid-column: 1;
		grid-row: 1;
}
#featured-mentors .cited-degree-program {
		text-transform: lowercase;
}

@media (max-width: calc(540px - 0.02px)) {
		#featured-mentors .mentor {
				display: flex;
				flex-direction: column;
		}
}
/*--------------------------------------------------------------
5. Featured project carousel
--------------------------------------------------------------*/
#featured-carousel .carousel .card-symposium {
		position: absolute;
		top: 2rem;
		right: 0rem;
		width: 26rem;
		height: auto;
		animation-name: fadeInRight;
		animation-duration: 0.65s;
		animation-delay: 1.5s;
}
#featured-carousel .carousel .featured-image {
		animation-name: fadeInLeft;
		animation-duration: 0.65s;
		animation-delay: 1s;
}
#featured-carousel .carousel .carousel-label {
		position: absolute;
		top: 1rem;
		left: 2rem;
}
#featured-carousel .carousel .carousel-controls {
		position: absolute;
		bottom: 0;
		left: 2rem;
		text-align: left;
}
#featured-carousel .carousel .carousel-controls .carousel-indicators {
		position: relative;
		margin: 1rem 0;
}
#featured-carousel .carousel .carousel-controls .carousel-indicators li {
		background-color: #bfbfbf;
}

#carousel-mobile-content {
		display: none;
}

@media (max-width: calc(992px - 0.02px)) {
		#featured-projects {
				display: none;
		}
		#carousel-mobile-content {
				display: block;
		}
}
/*--------------------------------------------------------------
6. FURI Alumni
--------------------------------------------------------------*/
.furi-alumni {
		background-color: #99e4ff;
		/* 80% saturated from ASU Blue. */
		background-image: url("../img/amountain.png");
		background-size: 55%;
		background-position: 100% 108%;
		background-repeat: no-repeat;
		padding: 4rem 0;
}
.furi-alumni div[class^=col-] > * {
