/*--------------------------------------------------------------
7. Life after FURI graph

// Mobile first, hide graph and inner label on mobile.
// Tablet sizes need further adjustment. (23-Feb-2023)
--------------------------------------------------------------*/
#life-after-furi {
		display: inline;
}
#life-after-furi #donutchart,
#life-after-furi h4 {
		display: none;
}
#life-after-furi .distribution {
		font-size: 3em;
		font-weight: 700;
		line-height: 1;
		z-index: 20;
}
#life-after-furi .distribution div {
		margin-left: 0;
}
#life-after-furi .distribution div p {
		display: table-cell;
		font-size: 14px;
		background-color: #ffc627;
		color: #222222;
		padding: 5px;
}

@media (min-width: calc(768px)) {
		#life-after-furi {
				display: grid;
				grid-template-columns: auto 27rem;
				grid-template-rows: 1fr 1fr;
		}
		#life-after-furi .intro {
				grid-column: 2;
				grid-row: 1;
				z-index: 20;
		}
		#life-after-furi .distribution {
				grid-column: 2;
				grid-row: 2;
				align-self: flex-end;
		}
		#life-after-furi #donutchart {
				display: block;
				grid-column: 1/span 2;
				grid-row: 1/span 2;
				width: 100%;
				min-height: 600px;
				z-index: 10;
		}
		#life-after-furi h4 {
				display: block;
				grid-column: 1;
				grid-row: 1/span 2;
				align-self: center;
				justify-self: center;
				font-size: 3em;
				font-weight: bold;
				line-height: 1;
				z-index: 20;
		}
		#life-after-furi h4 span {
				display: block;
				font-size: 6rem;
				line-height: 1;
		}
}
#life-after-furi .block-preview {
		background-image: url(../img/after-furi-placeholder.png);
		background-repeat: no-repeat;
		background-position: 6rem top;
		grid-column: 1;
		grid-row: 1/span 2;
		width: auto;
		min-height: 550px;
}

/*--------------------------------------------------------------
4. Snapshot Footer
--------------------------------------------------------------*/
#snapshot-footer {
		display: none;
}

@media (min-width: calc(992px)) {
		#snapshot-footer {
				display: block;
				color: #ffffff;
				display: grid;
				grid-template-columns: repeat(3, 1fr);
				gap: 2rem;
		}
		#snapshot-footer .stat-package {
				display: grid;
				grid-template-columns: 1fr 1fr;
				padding: 12px;
		}
		#snapshot-footer .stat-package h3 {
				margin: 0;
				font-size: 1.25rem;
				line-height: 0.95;
				color: #ffffff;
				font-weight: 700;
				text-align: right;
		}
		#snapshot-footer .stat-package h3 span {
				display: block;
				color: #ffc627;
		}
		#snapshot-footer .stat-package .counter {
				font-size: 2rem;
				line-height: 0.95;
				color: #ffffff;
				font-weight: 700;
				align-self: center;
				margin-left: 1rem;
		}
}
@media (min-width: calc(992px)) {
		#snapshot-global .stat-package h3 {
				font-size: 2rem;
		}
		#snapshot-global .stat-package .counter {
				font-size: 2.5rem;
				margin-left: 1rem;
		}
		#snapshot-global .stat-package.focus-areas h3 {
				margin: 1rem;
		}
}
@media (min-width: calc(1224px)) {
		#snapshot-global .stat-package h3 {
				font-size: 1.75rem;
				margin-left: 1rem;
		}
		#snapshot-global .stat-package .counter {
				font-size: 3.5rem;
				margin-left: 2rem;
		}
		#snapshot-global .stat-package.focus-areas h3 {
				margin-right: 2rem;
		}
}
